@tailwind base;
@tailwind components;
@tailwind utilities;

@import url("https://fonts.googleapis.com/css2?family=Raleway&display=swap");

@layer base {
    body {
        @apply font-[Raleway];
    }
    li{
        @apply px-4;
        @apply cursor-pointer;
        @apply hover:scale-125 cursor-pointer transition-all duration-150 ease-out
    }
    
}

.content-div{
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height: 250px;
}

.content-div:hover {
    background-image: linear-gradient(
        to right, 
        rgba(112,157,255,0.8),
        hsla(242,74%,61%,0.8)
    )!important;

}
@layer components{
    .btn{
        @apply h-7 hover:scale-125 cursor-pointer transition-all duration-150 ease-out ;
    }
}
@responsive {
    .underline-custom {
      @apply relative no-underline cursor-pointer;
    }
  
    .underline-custom::after {
      content: "";
      background: rgba(56, 178, 172, var(--text-opacity));
      @apply absolute left-0 bottom-0 w-full h-1 pt-1 transform origin-right scale-x-0 transition-transform duration-200 ease-in;
    }
  
    .underline-custom:hover::after,
    .underline-custom:focus::after {
      @apply transform origin-left scale-x-100;
    }
  }
  